<template>
  <div class="signlifeadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="3">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <tr>
                <td class="form_title align-middle" rowspan="16">
                  <span class="vertical-mode">生命徵象紀錄單</span>
                </td>
                <td>1</td>
                <td class="form_title align-middle">評估時間</td>
                <td>
                  <input type="time" id="time" name="time" class="form-control" v-model="list.time" />
                </td>
              </tr>
              <tr>
                <td>2</td>
                <td class="form_title align-middle">檢查結果</td>
                <td>
                  <input
                    type="text"
                    id="result"
                    name="result"
                    class="form-control"
                    v-model="list.result"
                  />
                </td>
              </tr>
              <tr>
                <td>3</td>
                <td class="form_title align-middle">體重</td>
                <td>
                  <input
                    type="text"
                    id="weight"
                    name="weight"
                    class="form-control"
                    v-model="list.weight"
                  />
                </td>
              </tr>
              <tr>
                <td>4</td>
                <td class="form_title align-middle">血壓(H)</td>
                <td>
                  <input type="text" id="bp_h" name="bp_h" class="form-control" v-model="list.bp_h" />
                </td>
              </tr>
              <tr>
                <td>5</td>
                <td class="form_title align-middle">血壓(L)</td>
                <td>
                  <input type="text" id="bp_l" name="bp_l" class="form-control" v-model="list.bp_l" />
                </td>
              </tr>
              <tr>
                <td>6</td>
                <td class="form_title align-middle">脈搏</td>
                <td>
                  <input
                    type="text"
                    id="pulse"
                    name="pulse"
                    class="form-control"
                    v-model="list.pulse"
                  />
                </td>
              </tr>
              <tr>
                <td>7</td>
                <td class="form_title align-middle">體溫</td>
                <td>
                  <input
                    type="text"
                    id="temperature"
                    name="temperature"
                    class="form-control"
                    v-model="list.temperature"
                  />
                </td>
              </tr>
              <tr>
                <td>8</td>
                <td class="form_title align-middle">呼吸</td>
                <td>
                  <input
                    type="text"
                    id="breath"
                    name="breath"
                    class="form-control"
                    v-model="list.breath"
                  />
                </td>
              </tr>
              <tr>
                <td>9</td>
                <td class="form_title align-middle">血糖</td>
                <td>
                  <input
                    type="text"
                    id="blood_sugar"
                    name="blood_sugar"
                    class="form-control"
                    v-model="list.blood_sugar"
                  />
                </td>
              </tr>
              <tr>
                <td>10</td>
                <td class="form_title align-middle">血氧濃度</td>
                <td>
                  <input
                    type="text"
                    id="blood_oxygen"
                    name="blood_oxygen"
                    class="form-control"
                    v-model="list.blood_oxygen"
                  />
                </td>
              </tr>
              <tr>
                <td>11</td>
                <td class="form_title align-middle">疼痛指數</td>
                <td>
                  <input type="text" id="pain" name="pain" class="form-control" v-model="list.pain" />
                  <!-- <select class="form-control" id="pain" name="pain">
              <option></option>
              <option>0分:沒有疼痛</option>
              <option>1~3分:輕度疼痛</option>
              <option>4~6分:中度疼痛</option>
              <option>7~10分:重度疼痛</option>
                  </select>-->
                </td>
              </tr>
              <tr>
                <td>12</td>
                <td class="form_title align-middle">GCS-E</td>
                <td>
                  <input
                    type="text"
                    id="gcs_e"
                    name="gcs_e"
                    class="form-control"
                    v-model="list.gcs_e"
                  />
                  <!-- <select class="form-control" id="gcs_e" name="gcs_e">
              <option></option>
              <option>1分</option>
              <option>2分</option>
              <option>3分</option>
              <option>4分</option>
                  </select>-->
                </td>
              </tr>
              <tr>
                <td>13</td>
                <td class="form_title align-middle">GCS-V</td>
                <td>
                  <input
                    type="text"
                    id="gcs_v"
                    name="gcs_v"
                    class="form-control"
                    v-model="list.gcs_v"
                  />
                  <!-- <select class="form-control" id="gcs_v" name="gcs_v">
              <option></option>
              <option>1分</option>
              <option>2分</option>
              <option>3分</option>
              <option>4分</option>
              <option>5分</option>
                  </select>-->
                </td>
              </tr>
              <tr>
                <td>14</td>
                <td class="form_title align-middle">GCS-M</td>
                <td>
                  <input
                    type="text"
                    id="gcs_m"
                    name="gcs_m"
                    class="form-control"
                    v-model="list.gcs_m"
                  />
                  <!-- <select class="form-control" id="gcs_m" name="gcs_m">
              <option></option>
              <option>1分</option>
              <option>2分</option>
              <option>3分</option>
              <option>4分</option>
              <option>5分</option>
              <option>6分</option>
                  </select>-->
                </td>
              </tr>
              <tr>
                <td>15</td>
                <td class="form_title align-middle">大便次數</td>
                <td>
                  <input
                    type="text"
                    id="stool"
                    name="stool"
                    class="form-control"
                    v-model="list.stool"
                  />
                </td>
              </tr>
              <tr>
                <td>16</td>
                <td class="form_title align-middle">備註</td>
                <td>
                  <input type="text" id="note" name="note" class="form-control" v-model="list.note" />
                </td>
              </tr>
            </table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{feeItems}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from 'dayjs';

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      loading: false,
      feeItems: [],
    };
  },
  props: ["viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });

    this.getFee();
  },

  computed: {
    bmi() {
      console.log("bmi");
      var bmi = 0;
      var height = this.$store.state.uData.height;
      var weight = this.list.weight;
      // ibw = 22*(parseInt(height)/100)*(parseInt(height)/100);
      bmi =
        parseInt(weight) /
        ((parseInt(height) / 100) * (parseInt(height) / 100));
      this.list.bmi = bmi.toFixed(2);
      return this.list.bmi;
    },
  },
  methods: {
    getFee() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        .then((rs) => {
          this.feeItems = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值 2020.8.1 這個不抓預設值
      // if (this.viewData[0] != undefined  ){
      //   this.list = { ...this.viewData[0] };
      // }
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/signlife",
            qs.stringify(this.list)
          )
          .then((rs) => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              // this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              //check血糖
              if (!!this.list.blood_sugar) {
                this.checkBloodSugar();
              }
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //判斷是否有血糖
    checkBloodSugar() {
      //判斷血糖->會計資料
      var feeData = this.feeItems.find((i) => {
        return i.name.includes("驗血糖/次");
      });
      if (!feeData) {
        alert("未建立[驗血糖/次]的會計項目,請通知會計或系統工程師!!");
        // var pop = { msg: "未建立[驗血糖/次]的會計項目,請通知會計或系統工程師!!", type: true, theme: "warning" };
        // this.$store.commit("snackbar", pop);
        return false;
      }

      var postData = {
        user_snkey: this.$store.state.uData.snkey,
        fee_snkey: feeData.snkey,
        date: this.list.date,
        start_day: this.list.date,
        end_day: this.list.date,
        quantity: 1,
        price: feeData.price,
        note: "(A)single",
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
      };

      /* 20201106判斷日期是否超過26日 */
      if (dayjs(postData.date).format('DD') >= 26){
        postData.date = dayjs(postData.date).add(1,'month').format('YYYY-MM-01');
      }
      /* 20201106判斷日期是否超過26日 end*/

      this.axios
        .post(
          "general/add/" + this.$store.state.databaseName + "/accounting",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = {
              msg: "已新增;[會計費用 -> 驗血糖/次 費用已新增]",
              type: true,
              theme: "success",
            };
            this.$store.commit("snackbar", pop);
            // this.$store.commit("setLoadingOff"); //讀取畫面off
          }
        });
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/signlife",
            qs.stringify(this.list)
          )
          .then((rs) => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              // this.$emit("parentData", this.list);
            }
          });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>