<template>
  <div class="signlifelist">
    <SignlifePrintOption ref="enterEdit"></SignlifePrintOption>
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>生命徵象紀錄單</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
              ></popupadd>

              <v-btn class="primary mx-1" @click="print">
                <v-icon class="mx-1">mdi-printer</v-icon>列印
              </v-btn>

            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead class="title">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="2">評估日期</th>
                        <th colspan="16">生命徵象紀錄單</th>
                        <th rowspan="2">紀錄人</th>
                        <th rowspan="2">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>1.評估時間</th>
                        <th>2.檢查結果</th>
                        <th>3.體重</th>
                        <th>4.血壓(H)</th>
                        <th>5.血壓(L)</th>
                        <th>6.脈搏</th>
                        <th>7.體溫</th>
                        <th>8.呼吸</th>
                        <th>9.血糖</th>
                        <th>10.血氧濃度</th>
                        <th>11.疼痛指數</th>
                        <th>12.GCS-E</th>
                        <th>13.GCS-V</th>
                        <th>14.GCS-M</th>
                        <th>15.大便次數</th>
                        <th>16.備註</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click.stop="enterNursingRecord(item)">
                                <v-list-item-title>轉入護理紀錄</v-list-item-title>
                              </v-list-item> -->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.time}}</td>
                        <td>{{item.result}}</td>
                        <td>{{item.weight}}</td>
                        <td>{{item.bp_h}}</td>
                        <td>{{item.bp_l}}</td>
                        <td>{{item.pulse}}</td>
                        <td>{{item.temperature}}</td>
                        <td>{{item.breath}}</td>
                        <td>{{item.blood_sugar}}</td>
                        <td>{{item.blood_oxygen}}</td>
                        <td>{{item.pain}}</td>
                        <td>{{item.gcs_e}}</td>
                        <td>{{item.gcs_v}}</td>
                        <td>{{item.gcs_m}}</td>
                        <td>{{item.stool}}</td>
                        <td>{{item.note}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData[0]}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/SignlifeAdd";
import SignlifePrintOption from "@/components/SignlifePrintOption"; //列印前的內容設定
import dayjs from "dayjs";

export default {
  components: { popupadd , SignlifePrintOption },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/signlife",
          qs.stringify(postData)
        )
        .then(rs => {
          // console.log(rs.data)
          this.items = this.viewData = this.sortBy(rs.data);
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    sortBy(data) {
      //依name排序
      var sortData = data.sort(function(a, b) {
        return a.date < b.date ? 1 : -1;
      });
      return sortData.sort(function(a,b){
        if (a.date == b.date){
          return a.time < b.time ? 1 : -1;
        }
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'signlife',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/signlife",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    //建構護理紀錄內容:
    enterNursingRecord(item) {
      var NursingRecordData = {
        date: item.date,
        u_snkey: item.u_snkey,
        nursingrecord_content:'[生命徵象內容]'+ '\n'
      };

      if (item.time != "") {
        NursingRecordData.nursingrecord_content +=
          "評估時間:" + item.time + "; ";
      }
      if (item.result != "") {
        NursingRecordData.nursingrecord_content +=
          "檢查結果:" + item.result + "; ";
      }
      if (item.weight != "") {
        NursingRecordData.nursingrecord_content += "體重:" + item.weight + "; ";
      }
      if (item.bp_h != "") {
        NursingRecordData.nursingrecord_content +=
          "血壓(H):" + item.bp_h + "; ";
      }
      if (item.bp_l != "") {
        NursingRecordData.nursingrecord_content +=
          "血壓(L):" + item.bp_l + "; ";
      }
      if (item.pulse != "") {
        NursingRecordData.nursingrecord_content += "脈搏:" + item.pulse + "; ";
      }
      if (item.temperature != "") {
        NursingRecordData.nursingrecord_content +=
          "體溫:" + item.temperature + "; ";
      }
      if (item.breath != "") {
        NursingRecordData.nursingrecord_content += "呼吸:" + item.breath + "; ";
      }
      if (item.blood_sugar != "") {
        NursingRecordData.nursingrecord_content +=
          "血糖:" + item.blood_sugar + "; ";
      }
      if (item.blood_oxygen != "") {
        NursingRecordData.nursingrecord_content +=
          "血氧濃度:" + item.blood_oxygen + "; ";
      }
      if (item.pain != "") {
        NursingRecordData.nursingrecord_content +=
          "疼痛指數:" + item.pain + "; ";
      }
      if (item.gcs_e != "") {
        NursingRecordData.nursingrecord_content += "GCS-E:" + item.gcs_e + "; ";
      }
      if (item.gcs_v != "") {
        NursingRecordData.nursingrecord_content += "GCS-V:" + item.gcs_v + "; ";
      }
      if (item.gcs_m != "") {
        NursingRecordData.nursingrecord_content += "GCS-M:" + item.gcs_m + "; ";
      }
      if (item.stool != "") {
        NursingRecordData.nursingrecord_content +=
          "大便次數:" + item.stool + "; ";
      }
      if (item.note != "") {
        NursingRecordData.nursingrecord_content += "備註:" + item.note + "; ";
      }

      //判斷護理紀錄資料是否存在
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };
      //取得該病歷所有護理紀錄資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingrecord",
          qs.stringify(postData)
        )
        .then(rs => {
          var matchData = rs.data.filter(i => {
            return i.date == item.date;
          });

          this.$store.commit("getTime"); //取得現在時間
          if (matchData.length == 0) {
            //新增
            NursingRecordData["create_man"] =
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")"; //執行動作的人,時間,內容

            this.axios
              .post(
                "general/add/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(NursingRecordData)
              )
              .then(rs => {
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄-已新增",
                    type: true,
                    theme: "success"
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          } else {
            //修改
            NursingRecordData["snkey"] = matchData[0].snkey;
            NursingRecordData["nursingrecord_content"] = matchData[0].nursingrecord_content+NursingRecordData.nursingrecord_content;
            NursingRecordData["edit_man"] =
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")"; //執行動作的人,時間,內容

            this.axios
              .post(
                "general/edit/" +
                  this.$store.state.databaseName +
                  "/nursingrecord",
                qs.stringify(NursingRecordData)
              )
              .then(rs => {
                console.log(rs.data);
                if (rs.data.state == 1) {
                  var pop = {
                    msg: "轉入護理紀錄資料-已追加",
                    type: true,
                    theme: "success"
                  };
                  this.$store.commit("snackbar", pop);
                }
              });
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    print() {
      this.$refs.enterEdit.$emit("childFn", this.items);
    },
  }
};
</script>


<style lang="scss">
.signlifelist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>